___CSS_LOADER_EXPORT___.push([module.id, `.container {
  display: flex;
  flex-direction: row;
  /* align-items: center; */
  /* justify-content: center; */
}

.container-login {
  /* position: absolute; */
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 60vh;
  width: 280px;
  background-color: white;
  border: 1px solid #bbb;
  border-radius: 15px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.3019607843), 0px 4px 8px 3px rgba(0, 0, 0, 0.1490196078);
  padding: 36px;
  margin: 0 24px;
}

.container-contexto {
  /* left: 320px; */
  /* flex-grow: 1; */
  background-color: orange;
  width: 100%;
  height: 100vh;
}

.input-titulo {
  position: relative;
  width: 100%;
  font-size: 1.25rem;
  font-weight: 700;
  padding: 18px 0px;
}

.input-footer {
  position: relative;
  width: 100%;
  text-align: center;
  a {
    padding: 4px;
    text-decoration: none;
  }
  a:hover {
    cursor: pointer;
  }
}

.input-container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .input-group {
    position: relative;
    display: flex;
    align-items: center;
    border: 1px solid #bbb;
    border-radius: 15px;
    padding: 6px 10px;
    background: #fff;
    width: 100%;
    margin: 6px 0;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
  }
  .input-group .material-symbols-rounded {
    color: #888;
  }
  .input-group input {
    border: none;
    outline: none;
    width: 100%;
    padding-left: 8px;
  }
  .input-group input:focus {
    outline: none;
  }
}

.error {
  position: relative;
  display: inline-block;
  color: #c0392b;
  font-size: 1.05rem;
  line-height: 110%;
  width: 80%;
  text-align: center;
  visibility: hidden;
  transition: var(--tr-duration-short) var(--tr-easing-linear);
  transition-property: visibility;
}

.error.active {
  visibility: visible;
}

.btn {
  border: 1px solid #888;
  border-radius: 15px;
  height: 32px;
  width: 100%;
  background-color: rgba(39, 114, 204);
  color: white;
  margin: 24px 0;
  .btn:hover {
    cursor: pointer;
  }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACI,aAAA;EACA,mBAAA;EACA,yBAAA;EACA,6BAAA;AACJ;;AAEA;EACI,wBAAA;EACA,OAAA;EACA,aAAA;EACA,sBAAA;EACA,uBAAA;EACA,mBAAA;EACA,YAAA;EACA,YAAA;EACA,uBAAA;EACA,sBAAA;EACA,mBAAA;EACA,oGACI;EAEJ,aAAA;EACA,cAAA;AADJ;;AAIA;EACI,iBAAA;EACA,kBAAA;EACA,wBAAA;EACA,WAAA;EACA,aAAA;AADJ;;AAIA;EACI,kBAAA;EACA,WAAA;EACA,kBAAA;EACA,gBAAA;EACA,iBAAA;AADJ;;AAIA;EACI,kBAAA;EACA,WAAA;EACA,kBAAA;EAEA;IACI,YAAA;IACA,qBAAA;EAFN;EAIE;IACI,eAAA;EAFN;AACF;;AAKA;EACI,kBAAA;EACA,WAAA;EACA,YAAA;EACA,aAAA;EACA,sBAAA;EACA,uBAAA;EACA,mBAAA;EAEA;IACI,kBAAA;IACA,aAAA;IACA,mBAAA;IACA,sBAAA;IACA,mBAAA;IACA,iBAAA;IACA,gBAAA;IACA,WAAA;IACA,aAAA;IACA,0CAAA;EAHN;EAME;IACI,WAAA;EAJN;EAOE;IACI,YAAA;IACA,aAAA;IACA,WAAA;IACA,iBAAA;EALN;EAQE;IACI,aAAA;EANN;AACF;;AASA;EACI,kBAAA;EACA,qBAAA;EACA,cAAA;EACA,kBAAA;EACA,iBAAA;EACA,UAAA;EACA,kBAAA;EACA,kBAAA;EACA,4DAAA;EACA,+BAAA;AANJ;;AASA;EACI,mBAAA;AANJ;;AASA;EACI,sBAAA;EACA,mBAAA;EACA,YAAA;EACA,WAAA;EACA,oCAAA;EACA,YAAA;EACA,cAAA;EAEA;IACI,eAAA;EAPN;AACF","sourcesContent":[".container {\r\n    display: flex;\r\n    flex-direction: row;\r\n    /* align-items: center; */\r\n    /* justify-content: center; */\r\n}\r\n\r\n.container-login {\r\n    /* position: absolute; */\r\n    left: 0;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    height: 60vh;\r\n    width: 280px;\r\n    background-color: white;\r\n    border: 1px solid #bbb;\r\n    border-radius: 15px;\r\n    box-shadow:\r\n        0px 1px 3px 0px #0000004d,\r\n        0px 4px 8px 3px #00000026;\r\n    padding: 36px;\r\n    margin: 0 24px;\r\n}\r\n\r\n.container-contexto {\r\n    /* left: 320px; */\r\n    /* flex-grow: 1; */\r\n    background-color: orange;\r\n    width: 100%;\r\n    height: 100vh;\r\n}\r\n\r\n.input-titulo {\r\n    position: relative;\r\n    width: 100%;\r\n    font-size: 1.25rem;\r\n    font-weight: 700;\r\n    padding: 18px 0px;\r\n}\r\n